.Time {
  width: 100%;
  align-self: flex-start;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (orientation: landscape) {
  .Time {
    font-size: 15vw;
    height: 75%;
  }
}

@media (orientation: portrait) {
  .Time {
    font-size: 30vw;
    height: 50vh;
    height: calc(1vh * 50);
    height: calc(var(--vh, 1vh) * 50);
  }
}

.Stopwatch {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.Stopwatch-count {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Stopwatch-actions {
  width: 100%;
  height: 25%;
  display: flex;
  flex-direction: row;
}

.Stopwatch-action {
  flex: 1 1;
  font-size: 3vw;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  border: none;
  color: inherit;
}

@media (orientation: landscape) {
  .Stopwatch {
    font-size: 15vw;
  }
  .Stopwatch-count {
    font-size: 10vw;
    height: 75%;
  }
  .Stopwatch-action {
    font-size: 5vw;
  }
}

@media (orientation: portrait) {
  .Stopwatch {
    font-size: 30vw;
    height: 50vh;
    height: calc(1vh * 50);
    height: calc(var(--vh, 1vh) * 50);
  }
  .Stopwatch-count {
    font-size: 20vw;
  }
  .Stopwatch-action {
    font-size: 10vw;
  }
}

.SplitClock {
  display: flex;
  min-height: 100vh;
  min-height: calc(1vh * 100);
  min-height: calc(var(--vh, 1vh) * 100);
}

.SplitClock-time,
.SplitClock-stopwatch {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.SplitClock-time {
  background-color: hsl(200, 80%, 90%);
}

.SplitClock-stopwatch {
  background-color: hsl(0, 80%, 90%);
}

@media (orientation: landscape) {
  .SplitClock {
    flex-direction: row;
  }
}

@media (orientation: portrait) {
  .SplitClock {
    flex-direction: column;
  }
}

.App {
  min-height: 100vh;
  min-height: calc(1vh * 100);
  min-height: calc(var(--vh, 1vh) * 100);
}

body {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
  -ms-scroll-chaining: none;
      overscroll-behavior: none;
}

button:focus {
  outline: none;
}

